<template>
  <div class="collection">
    <!-- 头部部分 -->
    <topHead
      :formId="formId"
      @deleteClick="deleteClick"
      @nullClick="nullClick"
      @addFormClick="addFormClick"
      @submittedClick="submittedClick"
      @fankuiClick="fankuiClick"
    ></topHead>
    <topSelect :formId="formId" @selectClick="selectClick"></topSelect>
    <div style="border: solid 1px #e0e0e0;margin: 0 10px;">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table
          :data="payChildTable"
          border
          style="max-width:100%;border: none"
          @selection-change="selsChange"
          :ref="formId+'Table'"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="employmentNumber" label="工号"></el-table-column>
          <el-table-column prop="personName" label="姓名"></el-table-column>
          <el-table-column prop="cardType" label="证照类型"></el-table-column>
          <el-table-column prop="cardNumber" label="证照号码" show-overflow-tooltip></el-table-column>
          <el-table-column prop="isBaosong" label="报送状态"></el-table-column>
          <el-table-column prop="isShenfen" label="身份验证状态"></el-table-column>
          <el-table-column prop="address" label="操作">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="updataRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="更新日期">
            <el-input :value="company[0].time_start"></el-input>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!-- 新增和修改表的弹窗 -->
    <el-dialog
      class="in-table"
      title="境内人员信息"
      :visible.sync="centerDialogVisible"
      :close-on-click-modal="close_modal"
      width="1000px"
      top="10vh"
    >
      <div class="in-table-content">
        <!-- 表单开始的地方 -->
        <el-form
          :status-icon="true"
          :model="payChildForm"
          :rules="rules"
          ref="payChildForm"
          label-width="80px"
          class="demo-ruleForm"
          label-position="right"
          size="mini"
        >
          <!-- 弹窗所有的表格信息 限定为600px高度-->
          <div style="height:600px;padding:0 30px 0 0px; overflow-y:auto">
            <div class="pl-20 mt-5 pb-10 line-bottom">
              <div class="mb-5">
                <span class="content-top-text-blue"></span>
                <span class="title text-red" style="line-height:40px;">温馨提示</span>
              </div>
            </div>
            <div class="ml-50 text-notice">1.子女教育包括年满3周岁至小学入学前的学前教育和全日制学历教育；</div>
            <div class="ml-50 text-notice">2.就读学校按规定组织实施的寒暑假、升学假期等可连续计算扣除；</div>
            <div class="ml-50 text-notice">3."教育终止时间"一经填写，从终止时间的次月起将不再享受子女教育专项附加扣除。</div>

            <!-- 个人信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left:2px">个人信息信息</span>
              </div>
              <div style="display:flex">
                <el-form-item v-if="isUpdata" label="姓名" style="width:33%">
                  <el-input :value="editValue" readonly></el-input>
                </el-form-item>
                <el-form-item v-else label="姓名" style="width:33%" prop="personInfoId">
                  <el-select v-model="payChildForm.personInfoId" @change="personChange(formId)" placeholder="-请选择姓名-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      :key="i"
                      :label="item.personName"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="证件类型" style="width:33%">
                  <el-input value="居民身份证"></el-input>
                </el-form-item>
                <el-form-item label="证件号码" prop="cardNumber" style="width:33%">
                  <el-select v-model="payChildForm.cardNumber" placeholder="-请选择证件号码-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      :key="i"
                      :label="item.cardNumber"
                      :value="item.cardNumber"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </div>
            <!-- 配偶信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">配偶信息</span>
              </div>
              <div style="display:flex">
                <el-form-item label="有无配偶" prop="resource">
                  <el-radio-group v-model="payChildForm.isMate">
                    <el-radio label="有配偶"></el-radio>
                    <el-radio label="无配偶"></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="姓名" style="width:33%">
                  <el-input v-model="payChildForm.mateName"></el-input>
                </el-form-item>
                <el-form-item label="证照类型" style="width:33%">
                  <el-input v-model="payChildForm.mateCardType" readonly></el-input>
                </el-form-item>
              </div>
              <el-form-item label="证照号码" style="width:33%">
                <el-input v-model="payChildForm.mateCardNumber"></el-input>
              </el-form-item>
            </div>
            <!-- 子女教育支出信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">子女教育支出信息</span>
              </div>
            </div>
            <el-button size="small" type="primary" @click="addTableCol">新增</el-button>
            <el-button size="small" @click="deleteTableCol(formId)">删除</el-button>
            <!-- 表格填写信息结束 -->
            <!-- 子女教育支出信息的表格 -->
            <el-table :data="payChildForm.tableChildPayData" border style="width: 100%;margin-top: 5px">
              <el-table-column prop="number" label="序号" width="50"></el-table-column>
              <el-table-column prop="name" label="子女姓名" width="120">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.childName"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="cardType" label="证照类型"></el-table-column>
              <el-table-column width="200" label="证照号码">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.cardNumber" @change="cardChange(scope.row)"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="出生日期">
                <template slot-scope="scope">
                  <el-date-picker
                    type="date"
                    v-model="scope.row.bothday"
                    placeholder
                    style="width: 100%;"
                    value-format="yyyy-MM-dd"
                  ></el-date-picker>
                  <!-- <el-input v-model="scope.row.bothday" ></el-input> -->
                </template>
              </el-table-column>
              <el-table-column prop="sitizenship" label="国籍（地区）" width="110"></el-table-column>
              <el-table-column prop="address" width="150" label="当前受教育阶段">
                <template slot-scope="scope">
                  <el-select v-model="scope.row.learnStatusNow">
                    <el-option value="学前教育阶段"></el-option>
                    <el-option value="义务教育"></el-option>
                    <el-option value="高中阶段教育"></el-option>
                    <el-option value="高等教育"></el-option>
                  </el-select>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="受教育日期起">
                <template slot-scope="scope">
                  <el-date-picker type="date" v-model="scope.row.learnStartDay" placeholder style="width: 100%;"
                                  value-format="yyyy-MM-dd"></el-date-picker>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="受教育日期止">
                <template slot-scope="scope">
                  <el-date-picker type="date" v-model="scope.row.learnEndDay" placeholder style="width: 100%;"
                                  value-format="yyyy-MM-dd"></el-date-picker>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="教育终止日期">
                <template slot-scope="scope">
                  <el-date-picker type="date" v-model="scope.row.studyEndDay" placeholder style="width: 100%;"
                                  value-format="yyyy-MM-dd"></el-date-picker>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="就读国家">
                <template>
                  <el-input value="中国"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="就读学校名称">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.schoolName"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="140" label="本人扣除百分比例">
                <template slot-scope="scope">
                  <el-select v-model="scope.row.percentDelete">
                    <el-option value="50%"></el-option>
                    <el-option value="100%"></el-option>
                  </el-select>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="150" label="扣除有效期起">
                <el-input :value="company[0].time_start" readonly></el-input>
              </el-table-column>
              <el-table-column prop="address" width="150" label="扣除有效期止">
                <el-input :value="company[0].time_end" readonly></el-input>
              </el-table-column>
              <el-table-column prop="address" width="120" label="采集来源">
                <template>
                  <el-input value="扣缴义务人"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="120" label="更新日期">
                <template>
                  <el-input :value="company[0].time_start"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="90" label="状态">
                <template>
                  <el-input value="待报送"></el-input>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <el-form-item class="btn-right">
            <el-button type="primary" size="small" @click="submitForm('payChildForm')">保存</el-button>
            <el-button size="small" @click="resetForm('payChildForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import formHelper from '../../until/formHelper'
import dataHelper from '../../until/dataHelper'
import commonData from '../../until/commonData'
import topHead from './components/top-head'
import topSelect from './components/top-select'
import check from '@/until/dataCheck'
import { mapGetters } from 'vuex'

var tableName = 'payChildForm'
export default {
  // name: 'payCollectionChild',
  components: {
    topHead,
    topSelect
  },
  data () {
    return {
      ...commonData,
      formId: tableName,
      close_modal: false,
      payChildForm: {// 表单的信息
        cardNumber: '',
        personInfoId: '',
        isMate: '',
        mateName: '',
        mateCardType: '居民身份证',
        mateCardNumber: '',
        tableChildPayData: [],
        isBaosong: '未报送',
        isShenfen: '未验证'
      },
      rules: {
        personInfoId: [
          { required: true, message: '请选择姓名' }
        ],
        cardNumber: [
          { required: true, message: '请选择身份证号' }
        ]
      },
      editValue: ''
    }
  },
  computed: {
    ...mapGetters(['company']),
    personInfo: {
      get () {
        return dataHelper.tableRelevance(this, 'personCollection', ['personName', 'cardNumber', 'cardType', 'infoId'], tableName)
      }
    },
    personCollection () {
      return this.$store.state.form.personCollection
    },
    payChildTable: {
      get () {
        if (!this.isSelectNow) {
          let b = dataHelper.tableRelevanceArr(this, this.$store.state.form[tableName], this.$store.getters.getFormContent('personCollection'), ['personName', 'cardNumber', 'cardType', 'employmentNumber'], 'personInfoId', 'infoId')
          return b
        } else {
          return this.selectArr
        }
      }
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
    console.log(this.company)
  },
  mounted () {
  },
  watch: {
    centerDialogVisible (val) {
      if (!val) {
        this.isUpdata = false
        this[tableName] = this.$options.data()[tableName]
      }
    }
  },
  methods: {
    ...formHelper,
    addFormClick () {
      this.centerDialogVisible = true
    },
    submitForm (formName) {
      var _this = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let submitData = {} // 用于提交的主键，格式参考store
          let payChildForm = _this[tableName]
          payChildForm.infoId = _this.$store.getters.getFormInfoId(formName) + 1
          submitData.formId = formName
          submitData.content = payChildForm
          if (_this.isUpdata) {
            _this.$store.commit('updataFormInfo', submitData)
            _this.isUpdata = false
          } else {
            _this.$store.commit('addFormInfo', submitData)
          }
          this.addMetaInfo(tableName)
          _this.centerDialogVisible = false
          this.payChildForm = this.$options.data().payChildForm
          _this.$message.success('提交成功!')
        } else {
          return false
        }
      })
    },
    addTableCol () {
      this.payChildForm.tableChildPayData.push({
        number: this.payChildForm.tableChildPayData.length,
        cardType: '身份证',
        sitizenship: '中国',
        bothday: '',
        childName: '',
        learnStartDay: '',
        learnEndDay: '',
        studyEndDay: '',
        schoolName: '',
        percentDelete: ''
      })
    },
    deleteTableCol () {
      this.payChildForm.tableChildPayData.pop()
    },
    handleCheckAllChange () {
      this.selectStatus = ['待报送', '待反馈', '报送成功', '报送失败']
    },
    handCancle () {
      this.selectStatus = []
    },
    updataRow (a, b) {
      this.centerDialogVisible = true
      this.payChildForm = this.$store.getters.getFormOneInfo(tableName, b.infoId)
      this.editValue = this.payChildForm.personName
      this.isUpdata = true
    },
    cardChange (a) {
      if (!check.checkCard(a.cardNumber)) {
        a.bothday = '身份证号码格式不正确'
      } else {
        a.bothday = dataHelper.getBirth(a.cardNumber)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    margin: 0;
  }

  img {
    vertical-align: middle;
  }

  .collection {
    font-size: 12px;
    text-align: start;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 0 0 30px;
  }

  .has-gutter th .cell {
    line-height: 30px !important;
  }

  .el-form-item {
    margin-bottom: 5px;
  }
</style>
<style>
  .el-button + .el-button {
    margin: 0;
  }

  .el-dialog__body {
    padding: 0;
  }

  .person-status .el-form-item__label {
    position: relative;
    float: right;
    right: 180px;
  }

  .el-form-item {
    margin-bottom: 18px;
  }

  .el-input__inner {
    height: 30px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }

  .top-btn .el-form-item__content {
    position: relative;
    right: 500;
    left: 500;
  }

  .el-form-item.el-form-item--feedback.el-form-item--mini {
    width: 294.69px;
  }

  .el-radio-group {
    position: relative;
    right: -35px;
  }
</style>



